import { faFacebookSquare, faTwitterSquare, faYoutubeSquare } from '@fortawesome/free-brands-svg-icons';
import { faCopyright } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { graphql, Link, useStaticQuery } from 'gatsby'
import { StaticImage } from 'gatsby-plugin-image';
import React, { useState } from 'react'
import Seo from './Seo.js'

const Layout = (props) => {
  const [navbarStatus, setNavbarStatus] = useState(false);
  /**
   * Hamburger menu controls
   * @param {*} event 
   */
  const handleNavbarClick = (event) => {
    setNavbarStatus((prevState) => {
      return !prevState;
    });
  }

  const footerData = useStaticQuery(graphql`
    {
      markdownRemark(frontmatter: {collectionKey: {eq: "footer"}}) {
        id
        frontmatter {
          title
          collectionKey
          description
          links {
            title
            href
          }
        }
      }
    }
  `)

  return (
    <div className="Layout">
      <Seo page={props.page} />
      <header>
        <nav className="navbar is-fixed-top is-black" role="navigation" aria-label="main navigation">
          <div className="container">
            <div className="navbar-brand">
              <Link to="/" className="navbar-item">Anasayfa</Link>
              <button className={`navbar-burger ${navbarStatus ? 'is-active' : ''}`} aria-label="menu" aria-expanded="false" onClick={handleNavbarClick}>
                <span aria-hidden="true"></span>
                <span aria-hidden="true"></span>
                <span aria-hidden="true"></span>
              </button>
            </div>
            <div className={`navbar-menu ${navbarStatus ? 'is-active' : ''}`}>
              <div className="navbar-end">
                <a href="#section-about" className="navbar-item">Hakkımızda</a>
                <a href="#section-productions" className="navbar-item">Yapımlar</a>
                <a href="#section-friends" className="navbar-item">Dostlarımız</a>
                <a href="#section-equipment" className="navbar-item">Ekipmanımız</a>
                <a href="#section-contact" className="navbar-item">İletişim</a>
              </div>
            </div>
          </div>
        </nav>
      </header>
      <main>
        {props.children}
      </main>
      <footer id="footer" className="footer has-background-black-bis has-text-light">
        <div className="container">
          <div className="content">
            <div className="has-text-centered mb-6">
              <span className="icon">
                <FontAwesomeIcon icon={faCopyright} size="sm" />
              </span>
              {" "} Stüdyo Tını
            </div>
            <ul>
              <li>
                {
                  footerData.markdownRemark.frontmatter.links.map((link, index) => {
                    return (
                      <span key={`footer-link-${index}`}>
                        <a href={link.href}>{link.title}</a>
                        {index !== footerData.markdownRemark.frontmatter.links.length-1 && " - "}
                      </span>
                    );
                  })
                }
              </li>
              <li>{props.settings.frontmatter.settings.email}</li>
              <li>
                <a href={`tel:${props.settings.frontmatter.settings.phone}`}>{props.settings.frontmatter.settings.phone}</a>
              </li>
              <li>{props.settings.frontmatter.settings.fax} (fax)</li>
              <li>
                <a href={props.settings.frontmatter.settings.facebook} rel="noreferrer nofollow">
                  <span className="icon">
                    <FontAwesomeIcon icon={faFacebookSquare} />
                  </span>
                    {" "} Facebook
                </a> {" - "}
                <a href={props.settings.frontmatter.settings.twitter} rel="noreferrer nofollow">
                  <span className="icon">
                    <FontAwesomeIcon icon={faTwitterSquare} />
                  </span>
                    {" "} Twitter
                </a> {" - "}
                <a href={props.settings.frontmatter.settings.youtube} rel="noreferrer nofollow">
                  <span className="icon">
                    <FontAwesomeIcon icon={faYoutubeSquare} />
                  </span>
                    {" "} YouTube
                </a>
              </li>
              <li>{props.settings.frontmatter.settings.address}</li>
              <li>
                <StaticImage
                  src="../../static/img/static-img-studyo-tini-logo.png"
                  alt="Stüdyo Tını Logo"
                  width={28}
                />
              </li>
              <li>{props.settings.frontmatter.settings.businessName}</li>
              <li>{props.settings.frontmatter.settings.taxNumber}</li>
            </ul>
          </div>
        </div>
      </footer>
    </div>
  )
}

export default Layout
