import React from 'react'
import { Helmet } from 'react-helmet'

const Seo = (props) => {
  const pageMeta = {
    title: 'Stüdyo Tını - Ses Kayıt & Prova Stüdyoları | Abdurrahman Tarikci',
    description: 'Stüdyo Tını Ses Kayıt ve Prova Stüdyoları'
  }

  if(props.page !== undefined) {
    pageMeta.title = props.page.frontmatter.title;
    pageMeta.description = props.page.frontmatter.description;
  }

  return (
    <Helmet>
      {/* Github Pages URL: https://xkema.github.io/com-studyotini/ */}
    
      {/* site meta */}
      <meta name="description" content={pageMeta.description} />
      <title>{pageMeta.title}</title>

      {/* meta meta 😱 */}
      <meta property="og:url" content="https://studyotini.com/" />
      <meta property="og:type" content="website" />
      <meta property="og:title" content={pageMeta.title} />
      <meta property="og:description" content={pageMeta.description} />
      <meta property="og:image" content="https://studyotini.com/img/static-img-studyo-tini-splash.png" />

      {/* twitter meta, missing tags will be completed from "og:*" meta */}
      <meta name="twitter:card" content="summary" />
      <meta name="twitter:site" content="@studyotini" />
      <meta name="twitter:image:alt" content="Stüdyo Tını Şirket Logosu" />

      {/* favicon */}
      <link rel="shortcut icon" href="https://studyotini.com/img/static-img-favicon.png"></link>

      {/* other */}
      <body className="has-navbar-fixed-top"></body>
    </Helmet>
  )
}

export default Seo
